<template lang="html">
  <v-app class="page">
    <v-container class="mainContainer margtop">
      <v-layout row class="text-xs-center pic mb-5" v-for="(item,i) in pics.slike" :key="i">
        <v-flex xs12>
          <clazy-load  :src="getPic(item.highQ)">
          <!-- The image slot renders after the image loads. -->
            <img :src="getPic(item.highQ)" slot="image">
          <!-- The placeholder slot displays while the image is loading. -->
            <div slot="placeholder">
              <img :src="getPic(item.lowQ)">
            </div>

          </clazy-load>
          <v-flex v-text="pics.text"></v-flex>
        </v-flex>
      </v-layout>
    </v-container>
  </v-app>
</template>

<script>
export default {
  props: ['id'],
  computed: {
    pics() {
      return this.$store.getters.LoadedAlbum(this.id)
    }
  },
    methods: {
      getPic(pic) {
        return require('@/assets/img/' + pic)
      }
    }
  }
</script>

<style scoped lang="css">

</style>



